import { Intent } from './constants';

/**
 * @name Intents
 * @type {Object}
 */
export default ({ createRequest }) => ({
  /**
   * @name Intents#initiatePayment
   * @description Intent to initiate a payment
   * @type {function}
   */
  initiatePayment: createRequest(Intent.INITIATE_PAYMENT),

  /**
   * @name Intents#initiateUSDomesticWirePayment
   * @description Intent to initiate a us domestic wire payment
   * @type {function}
   */
  initiateUSDomesticWirePayment: createRequest(Intent.INITIATE_US_DOMESTIC_PAYMENT),

  /**
   * @name Intents#initiateUSInternationalWirePayment
   * @description Intent to initiate a us international wire payment
   * @type {function}
   */
  initiateUSInternationalWirePayment: createRequest(Intent.INITIATE_US_FOREIGN_PAYMENT),
});



