/**
 * @name Hooks#defaultSortableColumn
 *
 * @description
 * Return the key of the default sort column
 *
 * @type {function}
 * @returns {string|null} Returns column key
 */
export const defaultSortableColumn = () => 'requestedExecutionDate';

/**
 * @name Hooks#defaultSortableDirection
 *
 * @description
 * defaultSortableDirection default hook
 *
 * @type {function}
 * @returns {?string}
 */
export const defaultSortableDirection = () => 'DESC';

/**
 * @name Hooks#truncateParams
 *
 * @description
 * truncateParams default hook
 *
 * @type {function}
 * @returns {object}
 */
export const truncateParams = params => {
  const myParams = {
    from: params.from,
    size: params.size,
    orderBy: params.orderBy,
    direction: params.direction,
  };
  if (params.query) {
    myParams.query = params.query;
  }
  return myParams;
};



