/**
 * @name Helpers
 * @description
 * Helpers for ext-bus-manage-payment-drafts-ng
 * @type {Object}
 */

const headers = [
  {
    label: 'header.debtor',
    sortable: {
      key: 'debtorName',
      direction: 'ASC',
    },
  },
  {
    label: 'header.creditor',
    sortable: {
      key: 'creditorName',
      direction: 'ASC',
    },
  },
  {
    label: 'header.currency',
    sortable: {
      key: 'currency',
      direction: 'ASC',
    },
  },
  {
    label: 'header.amount',
    class: 'text-right',
    sortable: {
      key: 'amount',
      direction: 'DESC',
    },
  },
  {
    class: '',
  },
];

/**
 * @name Helpers#isSortableActive
 *
 * @description
 * Checks if sortable is currently active
 *
 * @param {object} sortable object
 * @param {object} $ctrl the ctrl
 * @type {function}
 */
const isSortableActive = (sortable, $ctrl) =>
  sortable && (sortable.key === $ctrl.state.paymentDrafts.orderBy);

/**
 * @name Helpers#removeNotification
 *
 * @description
 * Helper to remove notification from the list
 *
 * @param {object} payment
 * @param {array} list
 * @type {function}
 * @returns {null}
 */
const removeNotification = (notification, notificationsList) =>
  notificationsList.filter((item) => item !== notification);

/**
 * @public
 * @name Helpers#isPaginationTypeMatch
 *
 * @description
 * Checks if actual pagination type matches the one, defined in properties
 *
 * @type {function}
 * @param {function} $ctrl      Current controller
 * @param {string} type         Description of pagination type (pagination, load-more)
 *
 * @returns {boolean}
 */
const isPaginationTypeMatch = ($ctrl, type) =>
  $ctrl.state.pageParams.paginationType === type;


/**
 * @public
 * @name Helpers#toggleAllDrafts
 *
 * @description
 * The select all action of the checkbox select column
 *
 * @type {function}
 * @param {array} drafts       All drafts
 * @param {string} checkAllState  Current state of select all dropdown
 */
const toggleAllDrafts = (drafts, checkAllState) =>
  drafts.forEach(draft => Object.assign(draft, { selected: checkAllState }));

/**
 * @public
 * @name Helpers#getSelectedDrafts
 *
 * @description
 * Filters the draft to only the selected ones
 *
 * @type {function}
 * @param {array} drafts  drafts
 */
const getSelectedDrafts = (drafts) =>
   drafts.filter(draft => draft.selected);

/**
 * @public
 * @name Helpers#onApplyFilter
 *
 * @description
 * Reloads the draft with applying a filter
 *
 * @type {function}
 * @param {object} drafts  ctrl
 */
const onApplyFilter = ($ctrl) => {
  $ctrl.load();
};

/**
 * @public
 * @name Helpers#deleteDraft
 *
 * @description
 * Deletes a draft and removes it from state
 *
 * @type {function}
 * @param {object} $ctrl the controller
 * @param {string} draftId draft to delete
 */
const deleteDraft = ($ctrl, draftId) => {
  $ctrl.deleteDraft(draftId).then(() => {
    $ctrl.reload();
  });
};

/**
 * @public
 * @name Helpers#deleteMultipleDrafts
 *
 * @description
 * Deletes multiple draft
 *
 * @type {function}
 * @param {object} $ctrl the controller
 * @param {array} draftsToDelete drafts to delete
 */
const deleteMultipleDrafts = ($ctrl, draftsToDelete) => {
  const promises = [];
  for (const draftToDelete of draftsToDelete) {
    promises.push($ctrl.deleteDraft(draftToDelete.id));
  }
  Promise.all(promises).then(() => {
    $ctrl.reload();
  });
};

/**
 * @public
 * @name Helpers#isPartiallySelected
 *
 * @description
 * Checks if some (but not all) drafts are selected
 *
 * @type {function}
 * @param {array} drafts to check for partial selection
 */
const isPartiallySelected = (drafts) => {
  const selectedDrafts = drafts.filter((draft) => draft.selected === true);
  return selectedDrafts.length !== 0 && selectedDrafts.length !== drafts.length;
};

/**
 * @public
 * @name Helpers#isSelectedAll
 *
 * @description
 * Checks if all drafts are selected
 *
 * @type {function}
 * @param {array} drafts to check for partial selection
 */
const isSelectedAll = (drafts) => {
  for (const draft of drafts) {
    if (!draft.selected) {
      return false;
    }
  }
  return true;
};

/**
 * @name Helpers#showDeleteDialog
 * @type {function}
 * @description
 * Show respective popup dialog for deleting draft(s)
 *
 * @param {object} $ctrl
 * @param {object} ext
 * @param {object|undefined} draft to be deleted (if a direct delete), optional
 */
const showDeleteDialog = ($ctrl, ext, draft) => {
  // eslint-disable-next-line no-param-reassign
  ext.draftsSelected = draft ? [draft] : getSelectedDrafts($ctrl.state.paymentDrafts.data);

  if (ext.draftsSelected.length > 1) {
    // eslint-disable-next-line no-param-reassign
    ext.confirmDeleteDrafts = true;
  } else {
    // eslint-disable-next-line no-param-reassign
    ext.confirmDeleteDraft = true;
  }
};

export default ({ intents, model }) => {
  const initiateIntentsMap = {
    [model.PaymentType.SEPA_CT]: intents.initiatePayment,
    [model.PaymentType.US_DOMESTIC_WIRE]: intents.initiateUSDomesticWirePayment,
    [model.PaymentType.US_FOREIGN_WIRE]: intents.initiateUSInternationalWirePayment,
  };

  /**
   * @name Helpers#initiatePayment
   * @type {Function}
   * @description
   * Initiate payment depending on the payment type
   * @param {Object} payment Payment to send to the initiate payment intent
   */
  const initiatePayment = (payment) => initiateIntentsMap[payment.paymentType](payment);

  return {
    headers,
    isSortableActive,
    removeNotification,
    isPaginationTypeMatch,
    toggleAllDrafts,
    getSelectedDrafts,
    onApplyFilter,
    deleteDraft,
    deleteMultipleDrafts,
    isPartiallySelected,
    isSelectedAll,
    showDeleteDialog,
    initiatePayment,
  };
};



